// Fonts
$mainFont: "Raleway", sans-serif;
$subFont: "Montserrat", sans-serif;

// Color Scheme
$primaryColor: #0d866a;
$secondaryColor: #f7edd5;
$inputColor: #bbbbbb;

// General Style
body {
  font-family: $subFont;
  background: linear-gradient(-45deg, #3e7a84, #963a53, #21a0bd, #23d5ab);
  background-size: 400% 400%;
  animation: gradient 20s ease infinite;
}
.container.login {
  max-width: 960px !important;
  margin: 6vh auto !important;
  padding: 0 !important;
}
@keyframes gradient {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}

a {
  display: inline-block;
  text-decoration: none;
}
a:hover {
  text-decoration: none;
}
p {
  font-size: 12px;
}
p.errorMsg {
  color: red;
}
input {
  outline: none !important;
}
h1 {
  text-align: center;
  text-transform: uppercase;
  margin-bottom: 40px;
  font-weight: 700;
  color: $primaryColor;
}
section#formHolder {
  padding: 5em 0;
}
//button
button.sign {
  background: #1aab8a;
  color: #fff;
  border: none;
  position: relative;
  height: 40px;
  padding: 0 2em;
  cursor: pointer;
  transition: 500ms ease all;
  outline: none;
  font-size: 14px;
  width: 100%;
  text-transform: uppercase;
  font-family: $subFont;
  margin: 20px 0;
  text-align: center;
}
button.sign:focus {
  outline: none;
}
button.sign:hover {
  background: #fff;
  color: #1aab8a;
}
button.sign:before,
button.sign:after {
  content: "";
  position: absolute;
  top: 0;
  right: 0;
  height: 2px;
  width: 0;
  background: #1aab8a;
  transition: 400ms ease all;
}
button.sign:after {
  right: inherit;
  top: inherit;
  left: 0;
  bottom: 0;
}
button.sign:hover:before,
button.sign:hover:after {
  width: 100%;
  transition: 800ms ease all;
}
// Brand Area
.brand {
  padding: 20px;
  background-image: url(/images/black.png);
  background-size: cover;
  background-position: center center;
  color: #fff;
  min-height: 540px;
  position: relative;
  box-shadow: 3px 3px 10px rgba(0, 0, 0, 0.3);
  transition: all 0.6s cubic-bezier(1, -0.375, 0.285, 0.995);
  z-index: 1;
  &.active {
    width: 100%;
  }
  a.logo {
    color: $primaryColor;
    font-size: 20px;
    font-weight: 700;
    text-decoration: none;
    line-height: 1em;
    span {
      font-size: 30px;
      color: #fff;
      transform: translateX(-5px);
      display: inline-block;
    }
  }
  .heading {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    text-align: center;
    transition: all 0.6s;
    &.active {
      top: 100px;
      left: 100px;
      transform: translate(0);
    }
    h2 {
      font-size: 70px;
      font-weight: 700;
      text-transform: uppercase;
      margin-bottom: 0;
      color: white;
    }
    p {
      font-size: 15px;
      font-weight: 300;
      text-transform: uppercase;
      letter-spacing: 2px;
      white-space: 4px;
      font-family: $mainFont;
    }
  }
  .success-msg {
    width: 100%;
    text-align: center;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    margin-top: 60px;

    p {
      font-size: 25px;
      font-weight: 400;
      font-family: $mainFont;
    }
    a {
      font-size: 12px;
      text-transform: uppercase;
      padding: 8px 30px;
      background: $primaryColor;
      text-decoration: none;
      color: #fff;
      border-radius: 30px;
    }
    p,
    a {
      transition: all 0.9s;
      transform: translateY(20px);
      opacity: 0;
    }
    &.active {
      transform: translateY(0);
      opacity: 1;
    }
  }
}
// Form Area
.form {
  position: relative;

  .form-peice {
    background: #fff;
    min-height: 480px;
    margin-top: 30px;
    box-shadow: 3px 3px 10px rgba(0, 0, 0, 0.2);
    color: $inputColor;
    padding: 30px 0 60px;
    transition: all 0.9s cubic-bezier(1, -0.375, 0.285, 0.995);
    position: absolute;
    top: 0;
    left: -30%;
    width: 130%;
    overflow: hidden;
    &.switched {
      transform: translateX(-100%);
      width: 100%;
      left: 0;
    }
  }
  form {
    padding: 0 40px;
    margin: 0;
    width: 70%;
    position: absolute;
    top: 50%;
    left: 60%;
    transform: translate(-50%, -50%);

    .form-group {
      margin-bottom: 5px;
      position: relative;
      &.hasError {
        input {
          border-color: $primaryColor !important;
        }
        label {
          color: $primaryColor !important;
        }
      }
    }
    label {
      font-size: 12px;
      font-weight: 400;
      text-transform: uppercase;
      font-family: $subFont;
      transform: translateY(40px);
      transition: all 0.4s;
      cursor: text;
      z-index: -1;
      &.active {
        transform: translateY(10px);
        font-size: 10px;
      }
      &.fontSwitch {
        font-family: $mainFont !important;
        font-weight: 600;
      }
    }
    input:not([type="submit"]) {
      background: none;
      outline: none;
      border: none;
      display: block;
      padding: 10px 0;
      width: 100%;
      border-bottom: 1px solid #eee;
      color: #444;
      font-size: 15px;
      font-family: $subFont;
      z-index: 1;
      &.hasError {
        border-color: $primaryColor;
      }
    }
    span.error {
      color: $primaryColor;
      font-family: $subFont;
      font-size: 12px;
      position: absolute;
      bottom: -20px;
      right: 0;
      display: none;
    }
    input[type="password"] {
      color: $primaryColor;
    }
  }
}
footer {
  text-align: center;
  p {
    color: #777;
    a,
    a:focus {
      color: #b8b09f;
      transition: all 0.3s;
      text-decoration: none !important;
      &:hover {
        color: $primaryColor;
      }
    }
  }
}
p.switch {
  color: black;
  font-size: 14px;
  span {
    font-weight: bold;
    cursor: pointer;
  }
}
@media (max-width: 1199px) {
  .container.login {
    max-width: 800px !important;
    margin: 9vh auto !important;
  }
}

@media (max-width: 768px) {
  .container {
    overflow: hidden;
  }
  .container.login {
    max-width: 700px !important;
  }
  .brand .heading h2 {
    font-size: 3rem;
  }
  section#formHolder {
    padding: 0;
    div.brand {
      min-height: 150px !important;
      &.active {
        min-height: 100vh !important;
      }
      .heading.active {
        top: 200px;
        left: 50%;
        transform: translate(-50%, -50%);
      }
      .success-msg {
        p {
          font-size: 16px;
        }
        a {
          padding: 5px 30px;
          font-size: 10px;
        }
      }
    }
    .form {
      width: 80vw;
      min-height: 500px;
      margin-left: 0;
      .form-peice {
        margin: 0;
        top: 0;
        left: 0;
        width: 100% !important;
        transition: all 0.5s ease-in-out;

        &.switched {
          transform: translateY(-100%);
          width: 100%;
          left: 0;
        }
        > form {
          width: 100% !important;
          padding: 60px;
          left: 50%;
        }
      }
    }
  }
}
@media (max-width: 480px) {
  section#formHolder .form {
    width: 100%;
    margin-left: 0;
  }
  h2 {
    font-size: 50px !important;
  }
}
